/**
 * Failed Webhooks Admin API
 * GET /api/admin/webhooks/failed - List failed webhooks
 * POST /api/admin/webhooks/failed/retry - Retry a failed webhook
 *
 * Admin-only endpoints for webhook recovery management
 */

import { NextRequest, NextResponse } from 'next/server';
import { getAuth } from '@clerk/nextjs/server';
import { db } from '@/lib/firebase';
import {
  getFailedWebhooks,
  retryFailedWebhook,
  cleanupExpiredWebhooks
} from '@/lib/webhook-recovery';
import { logInfo, logError } from '@/utils/logger';
import { validateAdminOrigin } from '@/lib/csrf-protection';

export const dynamic = 'force-dynamic';

/**
 * GET /api/admin/webhooks/failed
 * List failed webhooks with optional filters
 */
export async function GET(req: NextRequest): Promise<NextResponse> {
  try {
    // 1. Verify admin authentication
    const { userId } = await getAuth(req as any);
    if (!userId) {
      return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
    }

    // Check if user is admin
    const userDoc = await db.collection('users').doc(userId).get();
    if (!userDoc.exists || !userDoc.data()?.isAdmin) {
      return NextResponse.json({ error: 'Forbidden' }, { status: 403 });
    }

    // 2. Parse query parameters
    const { searchParams } = new URL(req.url);
    const source = searchParams.get('source') as 'twilio' | 'lob' | null;
    const status = searchParams.get('status') as 'failed' | 'retrying' | 'recovered' | 'abandoned' | null;
    const limit = parseInt(searchParams.get('limit') || '50', 10);

    // 3. Fetch failed webhooks
    const failedWebhooks = await getFailedWebhooks({
      source: source || undefined,
      status: status || undefined,
      limit,
    });

    return NextResponse.json({
      webhooks: failedWebhooks,
      count: failedWebhooks.length,
    });
  } catch (error: any) {
    logError('Error fetching failed webhooks', error);
    return NextResponse.json(
      { error: 'Internal server error' },
      { status: 500 }
    );
  }
}

/**
 * POST /api/admin/webhooks/failed/retry
 * Retry a failed webhook by ID
 */
export async function POST(req: NextRequest): Promise<NextResponse> {
  try {
    // 1. CSRF Protection: Validate origin
    if (!validateAdminOrigin(req)) {
      logError('CSRF: Invalid origin on admin webhook retry');
      return NextResponse.json({ error: 'Forbidden' }, { status: 403 });
    }

    // 2. Verify admin authentication
    const { userId } = await getAuth(req as any);
    if (!userId) {
      return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
    }

    // Check if user is admin
    const userDoc = await db.collection('users').doc(userId).get();
    if (!userDoc.exists || !userDoc.data()?.isAdmin) {
      return NextResponse.json({ error: 'Forbidden' }, { status: 403 });
    }

    // 2. Parse request body
    const body = await req.json();
    const { eventId } = body;

    if (!eventId) {
      return NextResponse.json({ error: 'Missing eventId' }, { status: 400 });
    }

    // 3. Get the failed webhook event
    const eventDoc = await db.collection('failed_webhooks').doc(eventId).get();
    if (!eventDoc.exists) {
      return NextResponse.json({ error: 'Event not found' }, { status: 404 });
    }

    const event = eventDoc.data();
    if (!event) {
      return NextResponse.json({ error: 'Invalid event' }, { status: 400 });
    }

    // 4. Define processor function based on source
    const processor = async (payload: any, headers: Record<string, string>) => {
      if (event.source === 'twilio') {
        // Import and call the Twilio webhook processor
        const { processTwilioWebhook } = await import('@/lib/webhook-processors/twilio');
        await processTwilioWebhook(event.eventType, payload);
      } else if (event.source === 'lob') {
        // Import and call the Lob webhook processor
        const { processLobWebhook } = await import('@/lib/webhook-processors/lob');
        await processLobWebhook(event.eventType, payload);
      } else {
        throw new Error(`Unknown webhook source: ${event.source}`);
      }
    };

    // 5. Retry the webhook
    const success = await retryFailedWebhook(eventId, processor);

    if (success) {
      logInfo('Webhook retry successful', { eventId, userId });
      return NextResponse.json({
        success: true,
        message: 'Webhook retry successful'
      });
    } else {
      return NextResponse.json({
        success: false,
        message: 'Webhook retry failed'
      }, { status: 500 });
    }
  } catch (error: any) {
    logError('Error retrying webhook', error);
    return NextResponse.json(
      { error: error.message || 'Internal server error' },
      { status: 500 }
    );
  }
}

/**
 * DELETE /api/admin/webhooks/failed/cleanup
 * Clean up expired failed webhooks
 */
export async function DELETE(req: NextRequest): Promise<NextResponse> {
  try {
    // 1. CSRF Protection: Validate origin
    if (!validateAdminOrigin(req)) {
      logError('CSRF: Invalid origin on admin webhook cleanup');
      return NextResponse.json({ error: 'Forbidden' }, { status: 403 });
    }

    // 2. Verify admin authentication
    const { userId } = await getAuth(req as any);
    if (!userId) {
      return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
    }

    // Check if user is admin
    const userDoc = await db.collection('users').doc(userId).get();
    if (!userDoc.exists || !userDoc.data()?.isAdmin) {
      return NextResponse.json({ error: 'Forbidden' }, { status: 403 });
    }

    // 2. Clean up expired webhooks
    const count = await cleanupExpiredWebhooks();

    logInfo('Cleaned up expired webhooks', { count, userId });

    return NextResponse.json({
      success: true,
      count,
      message: `Deleted ${count} expired webhook(s)`,
    });
  } catch (error: any) {
    logError('Error cleaning up webhooks', error);
    return NextResponse.json(
      { error: 'Internal server error' },
      { status: 500 }
    );
  }
}
